<script>
import Card from "./Card.vue";
import FindPassword from "../../components/Modals/findPasswordModal.vue";
import BacktrackModal from "../../components/Modals/BacktrackModal.vue";
import LikedPostModal from "../../components/Modals/LikedPostModal.vue";
import MyPostModal from "../../components/Modals/MyPostModal.vue";
import * as Toast from "../../plugins/toast";
export default {
  props: ["userInfo"],
  components: {
    Card,
    FindPassword,
    BacktrackModal,
    LikedPostModal,
    MyPostModal,
  },
  data() {
    return {
      fetchedUserInfo: null,
      fetchedBacktrackInfo: null,
      totalItems: 0,
      currentPage: 1,
      itemsPerPage: 10,
      headers: [
        { title: "No", key: "id" },
        { title: "", key: "title" },
        { title: "", key: "createdAt" },
      ],
      backtracks: [],
      search: "",

      provider: "Backtrack",
      passwordExpanded: false,
      password: null,
      passwordMessage: null,
      passwordIsValid: false,
      passwordError: false,
      nicknameExpanded: false,
      nickname: null,
      nicknameMessage: null,
      nicknameIsValid: false,
      nicknameError: false,
      emailExpanded: false,
      email: null,
      emailMessage: null,
      emailIsValid: false,
      emailError: false,
      isShaking: false,
      isFullScreen: false,
    };
  },
  created() {
    this.fetchUserInfo();
    this.fetchBacktrackInfo();
  },
  computed: {
    pageCount() {
      return Math.ceil(this.totalItems / this.itemsPerPage);
    },
  },
  methods: {
    onPageChange(newPage) {
      this.currentPage = newPage;
      this.fetchBacktrackInfo();
    },

    openPassword() {
      this.passwordExpanded = !this.passwordExpanded;
    },
    closePassword() {
      this.passwordExpanded = !this.passwordExpanded;
    },
    openNickname() {
      this.nicknameExpanded = !this.nicknameExpanded;
    },
    closeNickname() {
      this.nicknameExpanded = !this.nicknameExpanded;
    },
    openLikedPost() {
      this.$store.commit("toggleShowLikedPostModal", true);
    },
    openMyPost() {
      this.$store.commit("toggleShowMyPostModal", true);
    },

    async confirmAction() {
      const shouldContinue = window.confirm("  ?");
      return shouldContinue;
    },
    async showBacktrackData(item) {
      try {
        const response = await this.$axios.get(`/api/backtrack/detail`, {
          params: {
            id: item.selectable.id,
          },
          withCredentials: true,
        });
        const backtrackData = response.data.backtrackData;
        if (backtrackData) {
          this.$store.commit("toggleBacktrackModal", true);
          this.$store.commit("setChordData", backtrackData.backtrack);
          this.$store.commit("setBacktrackData", backtrackData);
        }
      } catch (error) {
        console.error("Error fetching item details:", error);
      }
    },
    async deleteUser() {
      try {
        const shouldContinue = await this.confirmAction();
        if (!shouldContinue) {
          return;
        }

        const response = await this.$axios.delete(
          `/api/mypage/`,

          {
            withCredentials: true,
          }
        );
        Toast.alertMessage(" .");
        setTimeout(() => {
          this.$router.push("/");
        }, 3000);
      } catch (error) {
        console.error("Failed to delete user info:", error);
      }
    },
    async changeNickname() {
      try {
        if (this.nickname.length > 15) {
          throw "  15  .";
        }

        if (
          /[&<>()'/"]/.test(this.nickname) ||
          /[\uD800-\uDBFF][\uDC00-\uDFFF]/.test(this.nickname)
        ) {
          throw "     .";
        }
        const response = await this.$axios.put(
          `/api/mypage/userInfo/nickname`,
          {
            nickname: this.nickname,
          },
          {
            withCredentials: true,
          }
        );
        this.nicknameIsValid = true;
        this.nicknameMessage = response.data;
        this.$router.go();
      } catch (error) {
        console.error("Failed to fetch nickname info:", error);
        this.isShaking = true;
        this.nicknameIsValid = false;
        this.nicknameError = true;
        if (error instanceof Error) {
          this.nicknameErrorMessage = error.response.data.message;
        } else {
          this.nicknameErrorMessage = error;
        }
        setTimeout(() => {
          this.isShaking = false;
          this.nicknameError = false;
        }, 3000);
      }
    },
    openEmail() {
      this.emailExpanded = !this.emailExpanded;
    },
    closeEmail() {
      this.emailExpanded = !this.emailExpanded;
    },
    async changeEmail() {
      try {
        const emailRegex = /[^@]+@[^\.]+\..+/;
        if (!emailRegex.test(this.email)) {
          throw "   .";
        }
        const response = await this.$axios.put(
          `/api/mypage/userInfo/email`,
          {
            email: this.email,
          },
          {
            withCredentials: true,
          }
        );
        this.emailIsValid = true;
        this.emailMessage = response.data;
        this.$router.go();
      } catch (error) {
        console.error("Failed to fetch user info:", error);
        this.isShaking = true;
        this.emailIsValid = false;
        this.emailError = true;
        if (error instanceof Error) {
          this.emailErrorMessage = error.response.data.message;
        } else {
          this.emailErrorMessage = error;
        }
        setTimeout(() => {
          this.isShaking = false;
          this.emailError = false;
        }, 3000);
      }
    },
    async fetchUserInfo() {
      try {
        const response = await this.$axios.get(
          `/api/mypage/userInfo`,

          {
            withCredentials: true,
          }
        );
        this.provider = localStorage.getItem("oauth");
        this.fetchedUserInfo = response.data;
        this.likedPosts = response.data.likedPosts;
        this.$store.commit("setLikes", response.data.likedPosts);
      } catch (error) {
        console.error("Failed to fetch user info:", error);
      }
    },
    async fetchBacktrackInfo() {
      try {
        const response = await this.$axios.get(
          `/api/backtrack?page=${this.currentPage}`,

          {
            withCredentials: true,
          }
        );

        this.fetchedBacktrackInfo = response.data;

        this.backtracks =
          this.fetchedBacktrackInfo.backtrackData.paginatedBacktracks;
        this.totalItems = this.fetchedBacktrackInfo.backtrackData.totalPage;
      } catch (error) {
        console.error("Failed to fetch user info:", error);
      }
    },

    async showFindPassword() {
      try {
        if (
          this.password === undefined ||
          this.newPassword === undefined ||
          this.newPasswordConfirm === undefined
        ) {
          throw "   .";
        }
        const response = await this.$axios.put(
          `/api/mypage/userInfo/password`,
          {
            password: this.originPassword,
            newPassword: this.newPassword,
            newPasswordConfirm: this.newPasswordConfirm,
          },
          {
            withCredentials: true,
          }
        );
        this.passwordIsValid = true;
        this.passwordMessage = response.data;
      } catch (error) {
        console.error("Failed to fetch user info:", error);
        this.isShaking = true;
        this.passwordIsValid = false;
        this.passwordError = true;
        if (error instanceof Error) {
          this.passwordErrorMessage = error.response.data.message;
        } else {
          this.passwordErrorMessage = error;
        }
        setTimeout(() => {
          this.isShaking = false;
          this.passwordError = false;
        }, 3000);
      }
    },
  },
};
</script>
